/*TOGGLE*/
.toggle-wrap {
  top: 20px;
  width: 50px;
  height: 43px;
  position: absolute;
  left: auto;
  right: 15px;
  z-index: 20;
  background: #a79d78;
}

.toggle-button {
  cursor: pointer;
  width: 50px;
  height: 43px;
  margin: 0;
  position: relative;
  border: none;
  z-index: 10;
  background: none;
  top: 0px;
  left: 0;
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
}
.toggle-button span {
  display: block;
  position: absolute;
  width: 24px;
  background: #FFF;
  opacity: 1;
  filter: alpha(opacity=100);
  right: 13px;
  height: 2px;
  border-radius: 0px;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.toggle-button span:nth-child(1) {
  top: 20px;
  -webkit-transform: rotate(135deg);
  -moz-transform: rotate(135deg);
  -o-transform: rotate(135deg);
  -ms-transform: rotate(135deg);
  transform: rotate(135deg);
}
.toggle-button span:nth-child(2) {
  opacity: 0;
  filter: alpha(opacity=0);
  top: 20px;
  right: -10px;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.toggle-button span:nth-child(3) {
  top: 20px;
  -webkit-transform: rotate(-135deg);
  -moz-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  transform: rotate(-135deg);
}
.toggle-button.collapsed span {
  -webkit-transform: rotate(0deg);
  -moz-transform: rotate(0deg);
  -o-transform: rotate(0deg);
  -ms-transform: rotate(0deg);
  transform: rotate(0deg);
}
.toggle-button.collapsed span:nth-child(1) {
  top: 12px;
}
.toggle-button.collapsed span:nth-child(2) {
  top: 20px;
  opacity: 1;
  filter: alpha(opacity=100);
  right: 13px;
}
.toggle-button.collapsed span:nth-child(3) {
  top: 28px;
}

